@tailwind base;
@tailwind components;
@tailwind utilities;
.webCamCaptureBtn{
  width: 40px;
  height: 40px;
  border: 2px solid black;
  border-radius: 50%;
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
}
.closeAll{
  width: 20px;
  height: 20px;
  position: relative;
  background-color: transparent;
}
.closeAll::after,
.closeAll::before{
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.closeAll::after{
  transform: rotate(45deg);
}
.closeAll::before{
  transform: rotate(-45deg);
}
.video{
  position: absolute; 
  right: 0; 
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
  width: auto; 
  height: auto; 
  background-size: cover;
  overflow: hidden;
}

